import React, {useEffect} from 'react';
import {
    AppBar,
    Button,
    IconButton,
    Toolbar,
    Typography,
    Switch,
    Menu,
    MenuItem,
    Container, styled,
} from '@mui/material';
import Brightness7Icon from '@mui/icons-material/Brightness7'; // Sun icon
import Brightness4Icon from '@mui/icons-material/Brightness4'; // Half moon icon
import MenuIcon from '@mui/icons-material/Menu';
import IAppProps from "./IAppProps";
import { useLocation } from 'react-router-dom';



const StyledToolbar = styled(Toolbar)(({theme}) => ({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '8px 12px',
    flexShrink: 0,
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    borderRadius: `calc(${theme.shape.borderRadius}px + 8px)`,
    boxShadow: '0 4px 30px rgba(0, 0, 0, 0.03)',
    backdropFilter: 'blur(10px)',
    border: `1px solid ${theme.palette.divider}`,

    [theme.breakpoints.down('sm')]: {
        backdropFilter: 'blur(5px)', // Reduce blur effect
        backgroundColor: 'rgba(255, 255, 255, 0.15)',
    },
}));

const HeaderComponent: React.FC<IAppProps> = ({mode, setMode}) => {
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const [pageName, setPageName] = React.useState<string | undefined>(undefined);
    const open = Boolean(anchorEl);

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(event.currentTarget);
    const handleClose = () => setAnchorEl(null);
    const toggleTheme = () => setMode(mode === 'light' ? 'dark' : 'light');

    const location = useLocation(); // Get the current location
    useEffect(() => {
        let currentPage: string | undefined = location.pathname;
        if (currentPage === '/') currentPage = 'Home';
        else currentPage = currentPage.split('/').pop();

        if (currentPage) currentPage = currentPage.charAt(0).toUpperCase() + currentPage.slice(1);

        setPageName(currentPage);  // Update the state with the page name
    }, [location]);

    return (
        <AppBar
            position="fixed"
            sx={{
                boxShadow: 0,
                bgcolor: 'transparent', // Transparent background for AppBar
                backgroundImage: 'none', // Remove background image if any
                mt: 'calc(var(--template-frame-height, 0px) + 20px)', // Adjust position
            }}
            color="default"
        >
            <Container maxWidth="lg"> {/* Restricts width and centers the AppBar */}
                <StyledToolbar variant="dense" disableGutters>
                    <IconButton
                        id="hamburger-nav-menu-button"
                        size="large"
                        edge="start"
                        color="primary"
                        aria-label="menu"
                        sx={{mr: 2}}
                        aria-controls={open ? 'menu-toggle' : undefined}
                        aria-haspopup="true"
                        aria-expanded={open ? 'true' : undefined}
                        onClick={handleClick}
                    >
                        <MenuIcon/>
                    </IconButton>
                    <Menu
                        id="hamburger-nav-menu"
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={handleClose}>Profile</MenuItem>
                        <MenuItem onClick={handleClose}>My account</MenuItem>
                        <MenuItem onClick={handleClose}>Logout</MenuItem>
                    </Menu>
                    <Typography variant="h6" component="div" sx={{flexGrow: 1}} color={'textSecondary'}>
                        {pageName}
                    </Typography>
                    <Button color="inherit" variant="text">
                        <Typography variant="inherit" component="div" sx={{flexGrow: 1}}
                                    color={'textSecondary'}>Login
                        </Typography>
                    </Button>
                    <Switch checked={mode === 'dark'} onChange={toggleTheme} inputProps={{'aria-label': 'controlled'}}
                            icon={<Brightness7Icon sx={{fontSize: 24, position: 'relative', bottom: '2px'}}/>}
                            checkedIcon={<Brightness4Icon sx={{fontSize: 24, position: 'relative', bottom: '2px'}}/>}/>
                </StyledToolbar>
            </Container>
        </AppBar>
    );
};

export default HeaderComponent;